// app/lib/api.ts

const API_BASE_URL =
  process.env.NEXT_PUBLIC_API_URL || "http://localhost:5000/api";

// Helper function untuk handle response
async function handleResponse(response: Response) {
  const result = await response.json();

  if (!response.ok) {
    throw new Error(result.error || result.message || "Something went wrong");
  }

  // Backend mengembalikan { success: true, data: {...} }
  // Atau { success: true, data: [...] } untuk array
  // Atau langsung array/object
  if (result.success && result.data !== undefined) {
    return result.data;
  }

  // Jika response langsung berupa data tanpa wrapper
  return result;
}

// Admin Authentication API
export const authAPI = {
  login: async (username: string, password: string) => {
    const response = await fetch(`${API_BASE_URL}/auth/login`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      credentials: "include",
      body: JSON.stringify({ username, password }),
    });
    const result = await response.json();

    if (!response.ok) {
      throw new Error(result.error || "Login failed");
    }

    // Return data dari backend: { success: true, data: { token, user } }
    return result.data || result;
  },

  verifyToken: async (token: string) => {
    const response = await fetch(`${API_BASE_URL}/auth/verify`, {
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
      credentials: "include",
    });
    return handleResponse(response);
  },
};

// Menu API
export const menuAPI = {
  // Get all menu items (Public)
  getAll: async () => {
    try {
      const response = await fetch(`${API_BASE_URL}/menu`, {
        method: "GET",
        headers: { "Content-Type": "application/json" },
      });

      const result = await response.json();

      console.log("Menu API Response:", result); // Debug log

      if (!response.ok) {
        throw new Error(result.error || "Failed to fetch menu");
      }

      // Handle berbagai format response
      if (result.success && result.data) {
        return result.data;
      }

      // Jika backend langsung return array
      if (Array.isArray(result)) {
        return result;
      }

      // Jika response adalah object dengan key lain
      return result.menu || result.data || result;
    } catch (error) {
      console.error("Menu API Error:", error);
      throw error;
    }
  },

  getById: async (id: string) => {
    const response = await fetch(`${API_BASE_URL}/menu/${id}`);
    return handleResponse(response);
  },

  create: async (menuData: any, token: string) => {
    const response = await fetch(`${API_BASE_URL}/menu`, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
      credentials: "include",
      body: JSON.stringify(menuData),
    });
    return handleResponse(response);
  },

  update: async (id: string, menuData: any, token: string) => {
    const response = await fetch(`${API_BASE_URL}/menu/${id}`, {
      method: "PUT",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
      credentials: "include",
      body: JSON.stringify(menuData),
    });
    return handleResponse(response);
  },

  delete: async (id: string, token: string) => {
    const response = await fetch(`${API_BASE_URL}/menu/${id}`, {
      method: "DELETE",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
      credentials: "include",
    });
    return handleResponse(response);
  },
};

// Order API
export const orderAPI = {
  // Get all orders (Admin only)
  getAll: async (token: string) => {
    try {
      const response = await fetch(`${API_BASE_URL}/orders`, {
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        credentials: "include",
      });

      const result = await response.json();

      console.log("Orders API Response:", result); // Debug log

      if (!response.ok) {
        throw new Error(result.error || "Failed to fetch orders");
      }

      // Handle berbagai format response
      if (result.success && result.data) {
        return result.data;
      }

      if (Array.isArray(result)) {
        return result;
      }

      return result.orders || result.data || result;
    } catch (error) {
      console.error("Orders API Error:", error);
      throw error;
    }
  },

  getById: async (id: string) => {
    try {
      console.log("üîç Fetching order by ID:", id);
      console.log("üì° API URL:", `${API_BASE_URL}/orders/${id}`);

      const response = await fetch(`${API_BASE_URL}/orders/${id}`);
      const result = await response.json();

      console.log("üì¶ Order response:", result);
      console.log("‚úÖ Response OK?", response.ok);

      if (!response.ok) {
        console.error("‚ùå Order fetch failed:", result.error);
        throw new Error(result.error || "Order not found");
      }

      // Backend return { success: true, data: {...} }
      if (result.success && result.data) {
        console.log("‚úÖ Returning result.data");
        return result.data;
      }

      console.log("‚úÖ Returning result directly");
      return result;
    } catch (error: any) {
      console.error("‚ùå Order getById Error:", error.message);
      throw error;
    }
  },

  create: async (orderData: any) => {
    try {
      console.log("üì§ Creating order with data:", orderData);

      const response = await fetch(`${API_BASE_URL}/orders`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(orderData),
      });

      const result = await handleResponse(response);

      console.log("‚úÖ Order created, response:", result);
      console.log(
        "üÜî Order ID from response:",
        result.order_id || result.id || result.data?.order_id
      );

      return result;
    } catch (error) {
      console.error("‚ùå Order create error:", error);
      throw error;
    }
  },

  updateStatus: async (id: string, status: string, token: string) => {
    try {
      // Status yang valid sesuai backend
      const validStatuses = [
        "pending",
        "confirmed",
        "processing",
        "ready",
        "completed",
        "cancelled",
      ];

      if (!validStatuses.includes(status)) {
        throw new Error("Invalid status: " + status);
      }

      const response = await fetch(`${API_BASE_URL}/orders/${id}/status`, {
        method: "PATCH",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        credentials: "include",
        body: JSON.stringify({ status }),
      });

      const result = await response.json();

      if (!response.ok) {
        throw new Error(result.error || "Failed to update status");
      }

      return result.data || result;
    } catch (error) {
      console.error("Update Status Error:", error);
      throw error;
    }
  },

  // Upload payment proof
  uploadPaymentProof: async (orderId: string, base64Image: string) => {
    try {
      console.log("üì§ Starting upload payment proof...");
      console.log("üÜî Order ID:", orderId);
      console.log("üìè Image size:", base64Image.length, "characters");
      console.log(
        "üìä Size estimate:",
        (base64Image.length / 1024).toFixed(2),
        "KB"
      );
      console.log("üîç First 50 chars:", base64Image.substring(0, 50));

      const url = `${API_BASE_URL}/orders/${orderId}/payment-proof`;
      console.log("üîó API URL:", url);

      const response = await fetch(url, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          payment_proof: base64Image,
        }),
      });

      console.log("üì° Response status:", response.status);
      console.log("üì° Response ok:", response.ok);

      // Coba parse response
      let result;
      const contentType = response.headers.get("content-type");
      console.log("üìÑ Content-Type:", contentType);

      if (contentType && contentType.includes("application/json")) {
        result = await response.json();
        console.log("üì¶ Response data:", result);
      } else {
        // Jika bukan JSON, ambil sebagai text
        const text = await response.text();
        console.error("‚ùå Response is not JSON:", text);
        throw new Error(
          "Server returned non-JSON response: " + text.substring(0, 100)
        );
      }

      if (!response.ok) {
        console.error("‚ùå Upload failed with status:", response.status);
        console.error("‚ùå Error details:", result);

        // Pesan error yang lebih detail
        const errorMessage =
          result.error ||
          result.message ||
          result.details ||
          `Upload failed with status ${response.status}`;

        throw new Error(errorMessage);
      }

      // Verify result has expected structure
      if (!result.success) {
        console.warn("‚ö†Ô∏è Success flag is false:", result);
        throw new Error(
          result.error || result.message || "Upload failed but no error message"
        );
      }

      console.log("‚úÖ Upload successful!");
      return result;
    } catch (error: any) {
      console.error("‚ùå Upload Payment Proof Error:", error.message);

      // Log stack trace untuk debugging
      if (error.stack) {
        console.error("Stack trace:", error.stack);
      }

      // Re-throw dengan pesan yang lebih jelas
      throw new Error(
        error.message ||
          "Failed to upload payment proof. Please check your connection and try again."
      );
    }
  },

  getByPhone: async (phone: string) => {
    try {
      console.log("üìû Fetching orders for phone:", phone);

      const response = await fetch(
        `${API_BASE_URL}/orders/customer/by-phone/${phone}`
      );
      const result = await response.json();

      console.log("üì¶ Orders response:", result);

      if (!response.ok) {
        throw new Error(result.error || "Failed to fetch orders");
      }

      return result.data || [];
    } catch (error: any) {
      console.error("‚ùå Error fetching orders:", error);
      throw error;
    }
  },
};

